body{
    font-family: 'Poppins', sans-serif;
}
  
.header{
    text-align:center;
    padding-top:50px;
    padding-bottom: 20px;
    font-size:2em;
}

#form1 input[type=text]{ 
    font-family: 'Poppins', sans-serif;
    width:420px ;
    height:80px;
    background: rgba(196, 196, 196, 0.31);
    border:none;
    margin:10px;
    border-radius: 10px;
    margin-left: 30%;
}
#form1 input:focus,
input:active {
  border-color: transparent;
}

::placeholder{ /*adding styling to the placholder text */
    font-family: 'Poppins', sans-serif;
    color:black;
    font-size: 1em;
    text-align: left;
}

.newAcc{
    font-family: 'Poppins', sans-serif;
    font-size:24px;
    width:480px;
    height:80px;
    border-radius: 20px;
    background: rgba(84, 161, 252, 1);
    line-height :48px;
    border:none;
    font-weight: 700;
    margin-top: 2%;
    margin-left: 28%;
}