#top-banner{
    width: 100%;
    height: 100px;

    background: #53ABEC;

    color:white;
    font-weight: 600;
    font-size: 30px;
    display: flex;

    text-align: center;
    justify-content:center;
    align-items: center;
}

#img{
    width:50%;
    height: 700px;
    display: inline-block;
    background-image: url('https://www.conceptstorage.com/media/zoo/images/warehouse-storage-benefits-2_72950a00bc17dda4f85c445ee2ef68c3.jpg');
    background-repeat: no-repeat;
    filter: blur(1px);
    filter: contrast(1);
    right: 20px;
    bottom: 0px;
}

#details{
    vertical-align: top;
    margin-left:5%;
    width:40%;
    height: 700px;
    display: inline-block;
    font-size: 23px ;
}
