
#form2{
  text-align:center;
  align-items: center;
  justify-content: center;
  border: 0px;
  margin:0px;
  width: 50%;
  /*margin-left: 30%;*/
}

#header{
  font-size: 1.75em;
  text-align:center;
  padding-bottom: 50px;
  color: rgba(0, 0, 0, 1);
}

#sign-in{
  background: rgba(84, 161, 252, 1);
  color: white;
  
  width:470px;
  height:80px;
  border-radius: 20px;
  border:0px;
  font-size: medium;
  font-weight:5600;
  margin-left: 60%;
}

#sign-in:hover{
  background: rgb(65, 128, 211,1);
  border: black 100px;
}

#new-user{
  text-decoration: solid;
  align-items: center;
  text-align: center;
  font-size:1.5em;
  line-height: 54px; 
  font-weight: 700;
  font-weight: bold;
}

#sign-up{
  font-family: 'Poppins', sans-serif;
  font-size:medium;
  width:460px ;
  height:80px;
  border-radius: 4px;
  background: rgba(84, 161, 252, 1);
  border-radius: 20px;
  line-height :36px;
  color:white;
  border: none;
  text-decoration: none;
  margin: 0px;
}

#form2 input[type=text]{ 
  font-family: 'Poppins', sans-serif;
  margin-left: 60%;
  width:460px ;
  height:80px;
  background:none;
  border:hidden;
  display: block;
  background: rgba(196, 196, 196, 0.31);
  border-radius: 10px;
}

#logout{
  align-items: center;

  font-family: 'Poppins', sans-serif;
  font-size:medium;
  width:460px ;
  height:80px;
  border-radius: 4px;
  background: rgba(84, 161, 252, 1);
  border-radius: 20px;
  line-height :36px;
  color:white;
  border: none;
  text-decoration: none;
  margin: 0px;
}


#user-info{
  align-items: center;
  font-size: 1.5em;
  text-align:center;
}