
#info{
    align-items: center;
    text-align: center;
}

#get-info input[type=text]{ 
    font-family: 'Poppins', sans-serif;
    width: 460px ;
    height:60px;
    background: rgba(196, 196, 196, 0.31);

    border:none;
    margin:10px;
    border-radius: 10px;
}

#get-info input[type=number]{ 
    font-family: 'Poppins', sans-serif;
    width: 90 px ;
    height:90 px;
    background: rgba(196, 196, 196, 0.31);
    grid-row: 2;
    border:none;
    margin:5px;
    line-height: 40px;
}

#get-info ::placeholder{ /*adding styling to the placholder text */
    font-family: 'Poppins', sans-serif;
    color:black;
    font-size: medium;
    text-align: left;
}

#get-info input[type= submit]{
    font-family: 'Poppins', sans-serif;
    font-size:medium;
    width:460px ;
    height:80px;
    border-radius: 4px;
    background: rgba(84, 161, 252, 1);
    border-radius: 20px;
    line-height :36px;
    color:white;
    border: none;
    text-decoration: none;
    margin: 30px;
}

#get-info{

   text-align:center;
   align-items: center;
   justify-content: center;
   border: 0px;
   margin-left:30%;
   width:30%;
}

#container{
    background: rgba(196, 196, 196, 0.31);
    border-radius: 10px;
    height: 250px;
    width: 280px;
    background-image: url('../assets/uspslogo.gif');
    background-size: 80px;
    background-repeat: no-repeat;
    /* background-position: right bottom; */
    background-position: 85% 85%;
    opacity: 0.7;
    backface-visibility: visible;
    margin: 0%;
    display: grid;
    margin: 10px;
    white-space: pre-line;
}

#card{
    font-size: 1em;
    text-align: center;

}
#card h3{
    margin: 0px;
    margin-top: 10px;
}


#back-button{
    font-family: 'Poppins', sans-serif;
    color:white;

    font-size:20px;
    width: 450px;
    height:80px;
    border-radius: 20px;
    background: rgba(84, 161, 252, 1);
    line-height :48px;
    border:none;

}